'use server';

/**
 * @fileOverview Coffee shop suggestion flow based on user's purpose.
 *
 * - suggestCoffeeShop - A function that suggests coffee shops based on user's purpose.
 * - CoffeeShopSuggestionInput - The input type for the suggestCoffeeShop function.
 * - CoffeeShopSuggestionOutput - The return type for the suggestCoffeeShop function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const CoffeeShopSuggestionInputSchema = z.object({
  purpose: z
    .string()
    .describe(
      'The purpose of the coffee shop visit (dating, learning, working, etc.).'
    ),
});
export type CoffeeShopSuggestionInput = z.infer<
  typeof CoffeeShopSuggestionInputSchema
>;

const CoffeeShopSuggestionOutputSchema = z.object({
  coffeeShopSuggestion: z
    .string()
    .describe('A coffee shop suggestion based on the user provided purpose.'),
});
export type CoffeeShopSuggestionOutput = z.infer<
  typeof CoffeeShopSuggestionOutputSchema
>;

export async function suggestCoffeeShop(
  input: CoffeeShopSuggestionInput
): Promise<CoffeeShopSuggestionOutput> {
  return suggestCoffeeShopFlow(input);
}

const prompt = ai.definePrompt({
  name: 'coffeeShopSuggestionPrompt',
  input: {schema: CoffeeShopSuggestionInputSchema},
  output: {schema: CoffeeShopSuggestionOutputSchema},
  prompt: `You are a coffee shop expert. You suggest coffee shops based on the user's stated purpose. Be very brief.

Purpose: {{{purpose}}}`,
});

const suggestCoffeeShopFlow = ai.defineFlow(
  {
    name: 'suggestCoffeeShopFlow',
    inputSchema: CoffeeShopSuggestionInputSchema,
    outputSchema: CoffeeShopSuggestionOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
