'use server';

import { suggestCoffeeShop, type CoffeeShopSuggestionInput } from '@/ai/flows/coffee-shop-suggestion';
import { z } from 'zod';

const SuggestionFormSchema = z.object({
  purpose: z.string().min(3, { message: "Purpose must be at least 3 characters long." }).max(100),
});

export interface SuggestionFormState {
  message?: string | null;
  suggestion?: string | null;
  errors?: {
    purpose?: string[];
  } | null;
  success: boolean;
}

export async function getCoffeeShopSuggestion(
  prevState: SuggestionFormState | undefined,
  formData: FormData
): Promise<SuggestionFormState> {
  const validatedFields = SuggestionFormSchema.safeParse({
    purpose: formData.get('purpose'),
  });

  if (!validatedFields.success) {
    return {
      errors: validatedFields.error.flatten().fieldErrors,
      message: 'Invalid input.',
      success: false,
    };
  }

  const input: CoffeeShopSuggestionInput = {
    purpose: validatedFields.data.purpose,
  };

  try {
    const result = await suggestCoffeeShop(input);
    return {
      message: 'Suggestion received!',
      suggestion: result.coffeeShopSuggestion,
      success: true,
    };
  } catch (error) {
    console.error('Error getting coffee shop suggestion:', error);
    return {
      message: 'Failed to get suggestion from AI. Please try again.',
      success: false,
    };
  }
}
